#back{
    position: absolute;
    width: 100%;
    height: 100%;
  z-index: -1;
  background: 
    linear-gradient(#685bfb,transparent), 
    linear-gradient(-55deg,#de8cf5,transparent), 
    linear-gradient(55deg,#77faf1,transparent); 
  }


  #back2{
    position: absolute;
    width: 100%;
    height: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.8);  
  }
  html, body{ 
    margin: 0;
    padding: 0;
    bottom: 0;
    box-sizing: border-box;
}

