
#login-frame1{
  z-index: 0;
  position: relative;


  display:inline-block;
  text-align: center;


  width: 26.8%;
  height: 30%;
  margin-left: 36%;
  margin-top: 5%;
}
#login-frame{

    position: relative;
    width: 100%;
    height: 100%;
    margin-top: -50%;

    display:grid;
   place-items: center;
   background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
    border-radius: 50px;
    z-index: -2;


}
#profile-img{
  margin-top: 2%;
}
#profile-circle{
  position: relative;
  fill: #ffffff;
  opacity: 0.9;
  margin-top: -100%;
}
#btn1 {
  position: relative;
  width: 40%;

  background: #ffffff;
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-radius: 20px;
  border-color: transparent;

  color: #7e7d7d;
  font-size: 1.25em;
  font-weight: 600;

  margin-top: 45%;
  padding:0.8em 2.8em;

  font-style: "Pretendard";
}

#btn2 {
  position: relative;
  width: 40%;


  background: #ffffff;
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-radius: 20px;
  border-color: transparent;

  color:  #7e7d7d;
  font-size: 1.25em;
  font-weight: 600;

  
  margin-top:8%;
  margin-bottom: 10%;

  padding:0.8em 2.8em;

  font-style: "Pretendard";
}

#btn3 {
  display: inline-block;
  position: relative;
  width: 60%;

  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

  border: 0;

  color: #8d8a8a;
  font-size: 1.25em;
  font-weight: 600;

  padding:0.8em 2.8em;
  font-style: "Pretendard";
}

#btn1:hover, #btn2:hover, #btn3:hover  {
  color: #ffffff;
  transition: color 300ms ease-in-out;
  background-color: #D8D7FB;
}


.inputField2~ #span2{
  font-family: "Pretendard";
  font-size: 1em;
  color: #3a3a3a;
  font-weight: 600;
  position: absolute;
  top: 79%;
  left: 20%;
  z-index: 1;
  transition: all ease-in-out 0.3s;
}
.inputField2:hover {
  border: 2px solid rgb(255, 255, 255);
}
.inputField2:focus-visible  {
  outline: none!important;
  border: 2px solid #8b81ff;
}
.inputField2:focus ~ #span2 {
  top : 71%;
  z-index: 1;
  font-size: 0.9em;
  transition: all ease-in-out 0.3s;
}

.inputField1~ #span1{
  font-family: "Pretendard";
  font-size: 1em;
  color: #3a3a3a;
  font-weight: 600;
  position: absolute;
  top: 54%;
  left: 22%;
  z-index: 1;
  transition: all ease-in-out 0.3s;
}
.inputField1:hover {
  border: 2px solid rgb(255, 255, 255);
}
.inputField1:focus-visible  {
  outline: none!important;
  border: 2px solid #8b81ff;
}
.inputField1:focus ~ #span1 {
  top : 46%;
  z-index: 1;
  font-size: 0.9em;
  transition: all ease-in-out 0.3s;
}


#document{
 
  margin-top: 3%;
  width: 80%;
  max-height: 90%;

  overflow-y: auto; 
  font-size: 1.5em;
  font-family: "Pretendard";
  color: #000000;
  background-color:  rgb(255, 255, 255, 0.75);
  border-bottom-left-radius: 30px;
  

  margin-left: 10%;
  padding-left: 1em;
}
#title1{
  margin-left: 42%;
  margin-top: 3%;
  margin-bottom: 3%;
  font-size: 2em;
}
#name{
  font-size: 3em;
  font-weight: 700;
}
#job{
  font-size: 1.5em;
  font-weight: 600;
}
#detail-info{
  float: right;
  margin-right: 1em;
  margin-top: 1em;
  font-size: 0.8em;
  border-collapse: collapse;
}
#middle-title{
  font-size: 1.5em;
  font-weight: 600;
}
table{
  text-align: center;
  border: 0.05em solid rgb(0, 0, 0);
  border-collapse: collapse;
}

#pjtable{
  width: 95%;
}
#sktable{
  width: 60%;
}

th,td{
  padding: 0.15em;
  border-bottom: 0.05em solid rgb(0, 0, 0);
  border-right: 0.05em solid rgb(0, 0, 0);
}

ul{
  list-style-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='5' y1='11.25' x2='19' y2='11.25' stroke='black' stroke-width='1.5'/%3E%3C/svg%3E%0A");
}

#pjexplain{
  font-size: 1.125em;
  font-weight: 600;
}

#homebtn{
  position: absolute;
  margin-top: 3%;
  top:0;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0.0625em 0em 2.5em rgba(112, 144, 176, 0.15);
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;

  margin-left: 6.1%;
  padding: 2.45em 1em 2em 1em;
  transition: width 250ms;

}

#homebtn:hover {

  background-color: #685bfb;
  box-shadow: none;
}